@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,300,400,500,700,900|Noto+Sans:400,400i,700,700i&display=swap&subset=korean);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-family: 'Noto Sans', sans-serif;
font-family: 'Noto Sans KR', sans-serif; */
*{
box-sizing: border-box;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
margin: 0;
padding: 0;
border: 0;
font-size: 18px;
color: #111;
font: inherit;
vertical-align: baseline;
font-family: 'Noto Sans KR', sans-serif;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
display: block;
}
body {
line-height: 1;
}
ol, ul {
list-style: none;
}
blockquote, q {
quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
content: '';
content: none;
}
table {
border-collapse: collapse;
border-spacing: 0;
}
a{
text-decoration: none;
}
.HomeMain{
    width: 100%;
    min-height: 3000px;
    background: #1b1b1b;
    padding-top: 108px;
}
.HomeMain__mainSlide{
    height: 530px;
    min-width: 1240px;
    max-width: 2340px;
    margin: 0 auto 50px;
    background: #1b1b1b;
    position: relative;
    overflow: hidden;
}
.HomeMain__mainSlide__slideWrapper{
    width: 100%;
    position: absolute;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    z-index: 10;
    transition: .35s;
}
.HomeMain__mainSlide__slideWrapper li{
    width:33%;
    min-width: 760px;
    background: #fefefe;
    margin-right: 30px;
    position: relative;
    flex-shrink: 0;
}
.HomeMain__mainSlide__slideWrapper li img{
    position: absolute;
    top: 0;
    left:50%;
    transform: translateX(-50%);
    height: 480px;
    width: 100%;
}
.HomeMain__mainSlide .prevBtn{
    background: url(data:image/png;base64,) no-repeat center/ cover;
    outline-style: none;
    border: none;
    position: absolute;
    top: 186px; left: 100px;
    width: 100px;
    height: 100px;
    z-index: 20;
    cursor: pointer;
    -webkit-filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
            filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
}
.HomeMain__mainSlide .prevBtn:hover{
    -webkit-filter: none;
            filter: none;
}
.HomeMain__mainSlide .nextBtn{
    background: url(data:image/png;base64,) no-repeat center/ cover;
    outline-style: none;
    border: none;
    position: absolute;
    top: 186px; right: 100px;
    width: 100px;
    height: 100px;
    z-index: 20;
    cursor: pointer;
    -webkit-filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
            filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
}
.HomeMain__mainSlide .nextBtn:hover{
    -webkit-filter: none;
            filter: none;
}
.HomeMain__mainSlide__indicator{
    width: 100%;
    height: 30px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
}
.HomeMain__mainSlide__indicator .controlBtn.play{
    width: 30px;
    height: 30px;
    outline-style: none;
    border: none;
    cursor: pointer;
    background: url(data:image/png;base64,) no-repeat center / cover;
}
.HomeMain__mainSlide__indicator .controlBtn.stop{
    width: 30px;
    height: 30px;
    outline-style: none;
    border: none;
    cursor: pointer;
    background: url(data:image/png;base64,) no-repeat center / cover;
}
.HomeMain__mainSlide__indicator span{
    width: 10px;
    height: 10px;
    background: #333333;
    border-radius: 100%;
    margin:0 3px;
    cursor: pointer;
}
.HomeMain__mainSlide__indicator span.active{
    background: #1351f9;
}
.HomeMain__bannerBar{
    width: 100%;
    height: 96px;
    margin: 0 auto 80px;
    background: rgb(25, 36, 56);
}
.HomeMain__bannerBar a{
    display: block;
    height: 96px;
    background: url(/react_wavve/static/media/banner.26b68fd6.jpg)no-repeat center;
}
.HomeMain__episode__wrapper{
    width: 1240px;
    margin: 0 auto;
    min-height: 327px;
    overflow: hidden;
}
.HomeMain__episode__top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.HomeMain__episode__title{
    margin-bottom: 20px;
    display: flex;
    align-items: baseline;
}
.HomeMain__episode__title h2{
    color: #fefefe;
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    margin-right: 10px;
}
.HomeMain__episode__title a{
    color: #a5a5a5;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
}
.HomeMain__episode__indicator{
    display: flex;
}
.HomeMain__episode__indicator span{
    width: 8px;
    height: 8px;
    background: #333333;
    border-radius: 100%;
    margin:0 3px;
    cursor: pointer;
}
.HomeMain__episode__indicator span.active{
    background: #1351f9;
}
.HomeMain__episode__slidewrapper{
    display: flex;
}
.HomeMain__episode__slidewrapper .thumbnail{
    width: 240px;
    height: 135px;
    margin-right: 10px;
}
.HomeMain__episode__slidewrapper .thumbnail img{
    width: 100%;
    border: 1px solid hsla(0,0%,65.9%,.15);
}
.HomeMain__episode__slidewrapper .title{
    width: 240px;
    background: #252525;
    padding: 10px 10px 20px;
    display: flex;
    flex-direction: column;
}
.HomeMain__episode__slidewrapper .title span{
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color:#a5a5a5;
    font-size: 16px;
    line-height: 24px;
}

body{
    background: #1b1b1b;
}
.mainWrapper{
    width: 100%;
}
header{
    width:100%;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 99;
}
.headBar{
    width: 100%;
    background: #1b1b1b;
    border-bottom: 1px solid rgba(235,235,235,0.1);
}
.headBar__wrapper{
    width: 1240px;
    margin: 0 auto;
}
.headBar__wrapper ul{
    height: 33px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.headBar__wrapper ul li{
    color: #a5a5a5;
    font-size:12px;
    padding: 0 11px;
    cursor: pointer;
    line-height: 34px;
    position: relative;
}
.headBar__wrapper ul li:after{
    content: '';
    position: absolute;
    top:12px;right: 0;
    width: 1px;
    height: 10px;
    background: #2f2f2f;
}
.headBar__wrapper ul li:last-child:after{
    background:none;
}
.mainNav{
    width: 100%;
    height: 70px;
    background: #1b1b1b;
}
.mainNav__wrapper{
    width: 1240px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.mainNav h1 img{
    cursor: pointer;
    width: 111px;
}
.mainNav__category{
    width:690px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: none;
    z-index: 98;
    margin-left: 35px;
}
.mainNav__category li{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1.15rem;
    cursor: pointer;
    position: relative;
    margin: 0 18px;
    line-height: 75px;
}
.mainNav__category li:hover{
    opacity: 1;
}
.mainNav__category li.clicked{
    opacity: 1;
}
.mainNav__searchBtn{
    background: none;
    outline-style: none;
    border: none;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.mainNav__searchBtn img{
    width: 33px;
}
.mainNav__searchBtn span{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1rem;
}
.mainNav__catBtn{
    background: none;
    outline-style: none;
    border: none;
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 70px;
}
.mainNav__catBtn img{
    width: 33px;
    opacity: 0.6;
}
.mainNav__catBtn:hover img{
    opacity: 1;
}
.mainNav__catBtn span{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1rem;
}
.mainNav__catBtn:hover span{
    opacity: 1;
}

.MainContents{
    width: 100%;
    height: 3000px;
    background: #1b1b1b;
}
.MainContents h2{
    padding-top: 300px;
    text-align: center;
    font-size: 2rem;
    color: #fefefe;
}
.mainNav__categoryBox{
    position: absolute;
    right: 0;
    top: 100%;
    min-width: 381px;
    padding: 20px;
    background: #101010;
    transform: translateY(-1px);
    display: flex;
    transition: all .3s linear;
    opacity: 0;
    visibility: hidden;
}
.mainNav__categoryBox__title{
    width: 100px;
}
.mainNav__categoryBox__title li{
}
.mainNav__categoryBox__title li a{
    color: #a5a5a5;
    line-height: 34px;
    font-weight: 700;
    font-size: 14px;
}
.mainNav__categoryBox__title li a.selected{
    color: #fefefe;
}
.mainNav__categoryBox__contents{
    position: relative;
}
.mainNav__categoryBox__contents:before{
    content: '';
    display: block;
    position: absolute;
    width: 1px;
    height: 100%;
    background: #2f2f2f;
}
.mainNav__categoryBox__contents li{
    width: 240px;
}

.mainNav__categoryBox__contents li ul li{
    display: inline-block;
    width: 100px;
    margin-left: 20px;
}
.mainNav__categoryBox__contents li ul li a{
    font-size: 14px;
    line-height: 34px;
    color: #a5a5a5;
}
.mainNav__categoryBox__contents li ul li a:hover{
    color: #fefefe;
}
